.contact-card__header {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.contact-card__title {
  text-transform: uppercase; }

.contact-card__content {
  padding: 20px 0 10px; }

.contact-card__bio {
  font-style: italic; }

.contact-card__contact {
  margin: 20px 0 0; }

.links-card__title {
  text-transform: uppercase; }

.links-card__content {
  padding: 20px 30px 10px; }

.links-card_sites {
  list-style: none; }

.links-card__site {
  margin: 0 0 5px;
  display: flex;
  align-content: center; }

.links-card__icon {
  height: 25px;
  margin: 0 10px 0 0; }

.projects-card__title {
