.theme-selector {
  position: fixed;
  top: 0;
  right: 0; }
  .theme-selector__toggle-button {
    background-color: #091921;
    border: none;
    border-radius: 50%;
    margin: 20px;
    padding: 10px;
    cursor: pointer;
    outline-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    z-index: 5;
    transition: background-color 0.3s linear; }
    .theme-selector__toggle-button:hover {
      opacity: 0.9; }

.theme-dark .theme-selector__toggle-button {
  background-color: #fff; }

